.App-link {
  color: #61dafb;
  text-decoration:none;
  margin-right:20px;
}
.active-link{
  border:1px solid gray;
  border-radius:5px;
}

table{
  border:1px solid gray;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
